@mixin link-styles($color, $hover-color) {
    color: $color;
    &:hover {
        color: $hover-color; } }
.link {
    font-weight: normal;
    font-size: 13px;
    transition: color .25s ease;
    &_black {
        @include link-styles($black, $light-green); }
    &_white {
        @include link-styles(#fff, $light-green); }
    &_green {
        @include link-styles($light-green, #fff); } }
